<template>
  <div class="about">
    <!-- id -->
    <h1>{{id ? '' : ''}}</h1>
      <!--   label-width="120px"  @submit.native.prevent="save"-->
    <el-form label-width="120px" @submit.native.prevent="save">
      <el-form-item label="">
        <!--   -->
        <el-select v-model="model.parent">
          <el-option v-for="item in parents" :key="item._id"
            :label="item.name" :value="item._id">
          </el-option>
        </el-select>

      </el-form-item>
      <el-form-item label="">
        <!--   -->
        <el-input v-model="model.name"></el-input>
      </el-form-item>
      <!--   -->
      <el-form-item>
        <el-button type="primary" native-type="submit"></el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>

export default {
  // router.jsid 
  props: {
    id: {}
  },
    // modelinput
  data(){
    return {
      model: {},
      parents: []
    }
  },
  // axioshttp.js
  methods: {
    //  
    async save(){
      let res
      if (this.id) {
        res = await this.$http.put(`rest/categories/${this.id}`, this.model)
      } else {
      // this.$http server
        let res = await this.$http.post('rest/categories', this.model)
      }

      //  /categories/list
      this.$router.push('rest/categories/list')
      //  
      this.$message({
        type: 'success',
        message: ''
      })
    },
    // id
    async fetch(){
      const res = await this.$http.get(`rest/categories/${this.id}`)
      this.model = res.data
      },
    // 
    async fetchParents(){
      const res = await this.$http.get(`rest/categories`)
      this.parents = res.data
      },

    },
    // id
  created(){
    this.fetchParents()
    this.id && this.fetch()
   }
  
}
</script>